@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap');

@font-face {
  font-family: 'Squad Heavy';
  src: local('Squad  Heavy'), local('Squad--Heavy'),
  url('../assets/fonts/Squad/Squad-Heavy.woff2') format('woff2'),
  url('../assets/fonts/Squad/Squad-Heavy.woff') format('woff'),
  url('../assets/fonts/Squad/Squad-Heavy.ttf') format('truetype');
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'Squad Regular';
  src: local('Squad  Regular'), local('Squad--Regular'),
  url('../assets/fonts/Squad/Squad-Regular.woff2') format('woff2'),
  url('../assets/fonts/Squad/Squad-Regular.woff') format('woff'),
  url('../assets/fonts/Squad/Squad-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}