@import 'buttons';

.product {
  max-width: 1280px;
  margin: 0 auto;

  &-main-area {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 100%;
    margin: auto 0;
    width: 100%;

    a {
      text-decoration: underline;
      font-weight: bold;
      cursor: pointer;
      color: #000000;
    }
  }

  &-presentation {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    z-index: 1;
    height: 501px;


    @include media-breakpoint-up(md) {
      height: 545px;
    }

    &__image {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 99;
      background: url('../assets/images/main-photo-mobile.png') no-repeat;
      background-size: cover;


      @include media-breakpoint-up(xs) {
        background: url('../assets/images/main-photo.jpg') no-repeat center bottom;
        background-size: cover;
      }
    }

    &__logo {
      position: absolute;
      bottom: 0;
      left: 11px;
      width: 504px;
      height: 252px;
      z-index: 100;
      display: none;
      background-size: 100%;
      background: url('../assets/images/otp-logo-bottom.svg') no-repeat 0 0;
      opacity: 84.23%;

      @include media-breakpoint-up(md) {
        display: block;
      }
    }

    &__info-block {
      display: flex;
      flex-direction: column;
      z-index: 100;
      padding-top: 192px;

      @include media-breakpoint-up(md) {
        padding-top: 152px;
      }
    }

    &__title {
      font-family: 'Squad Heavy', sans-serif;
      color: $white;
      font-weight: 800;
      font-size: 32px;
      line-height: 40px;
      margin: 0;


      @include media-breakpoint-up(md) {
        font-weight: 800;
        font-size: 40px;
        line-height: 48px;
        padding-top: 0;
        width: 724px;
      }
    }

    &__description {
      font-family: 'Squad Regular', sans-serif;
      font-weight: 800;
      width: 270px;
      padding: 16px 0 24px;
      color: $white;
      letter-spacing: 0.2px;
      font-size: 18px;
      line-height: 24px;

      @include media-breakpoint-up(md) {
        padding: 16px 0 37px;
        width: 500px;
      }

      @include media-breakpoint-up(lg) {
        font-size: 24px;
        line-height: 32px;
      }
    }

    &__btn-container {
      padding-bottom: 45px;

      @include media-breakpoint-up(md) {
        padding-bottom: 0;
      }
    }
  }

  &-advantages {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    margin: 24px 0;

    @include media-breakpoint-up(lg) {
      margin: 0 65px;
    }

    &__business {
      flex: 0 0 100%;
      max-width: 100%;
      color: #000000;

      @include media-breakpoint-up(lg) {
        height: 638px;
        flex: 0 0 50%;
        max-width: 50%;
      }

      ul {
        li {
          font-size: 16px;
          line-height: 24px;

          .big-text {
            font-size: 36px;
            line-height: 40px;
            padding-right: 16px;
            font-weight: 600;

            @include media-breakpoint-up(lg) {
              font-size: 32px;
            }
          }

          @include media-breakpoint-up(lg) {
            font-size: 20px;
            line-height: 40px;
          }
        }
      }
    }

    &__client {
      flex: 0 0 100%;
      max-width: 100%;

      @include media-breakpoint-up(lg) {
        height: 638px;
        flex: 0 0 50%;
        max-width: 50%;

      }
    }

    &__title {
      font-family: 'Squad Heavy', sans-serif;
      font-weight: 800;
      padding-top: 40px;
      width: auto;
      font-size: 24px;
      line-height: 32px;
      margin: 0 14px 0 0;


      @include media-breakpoint-up(lg) {
        width: max-content;
        font-size: 32px;
        line-height: 40px;
        padding-top: 80px;
      }

      &__description-business {
        padding-top: 32px;
      }
    }

    &__description-business {
      padding-top: 32px;
      padding-bottom: 110px;

      @include media-breakpoint-up(md) {
        padding-top: 37px;
        padding-bottom: 0;
        width: 400px;
      }

      ul {
        li {
          font-family: 'Source Sans Pro', sans-serif;
          padding-bottom: 14px;
          color: $black;
          font-weight: 700;
          line-height: 40px;
          margin-left: 0;

          @include media-breakpoint-up(lg) {
            margin-left: 32px;
          }

          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }

    &__description-client {
      padding-top: 32px;
      padding-bottom: 104px;

      @include media-breakpoint-up(md) {
        padding-top: 43px;
        padding-bottom: 0;
        width: 400px;
      }

      ul {
        li {
          font-family: 'Source Sans Pro', sans-serif;
          padding-bottom: 20px;
          color: $black;
          display: inline-flex;
          margin-left: 0;
          line-height: 26px;

          @include media-breakpoint-up(lg) {
            margin-left: 32px;
          }

          &:last-child {
            padding-bottom: 0;
          }
        }

        .circle-list {
          position: relative;
          top: 6px;
          width: 18px;
          height: 16px;
          margin-right: 16px;
          background: url('../assets/images/check-circle.svg') no-repeat;

        }

        .client-text {
          width: 100%;

          @include media-breakpoint-up(lg) {
            width: 315px;
          }
        }
      }
    }
  }

  &-sale {
    height: 100%;
    padding-bottom: 72px;

    @include media-breakpoint-up(lg) {
      height: 600px;
    }

    &__number {
      font-family: "Squad Heavy", sans-serif;
      font-weight: 800;
      font-size: 24px;
      line-height: 32px;
      color: #52AE32;
      display: flex;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      border: 1px solid #52AE32;
      box-sizing: border-box;
      align-items: center;
      justify-content: center;
    }

    &__title {
      padding-top: 80px;
      padding-bottom: 40px;
      font-family: 'Squad Heavy', sans-serif;
      font-style: normal;
      font-weight: 800;
      font-size: 32px;
      line-height: 40px;
      margin: 0;
      width: 100%;

      @include media-breakpoint-up(lg) {
        font-weight: 800;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: -0.4px;
        padding-top: 0;
        padding-bottom: 64px;
        width: 640px;
      }
    }

    &__box-container {
      display: flex;
      overflow-x: auto;
      overflow-y: hidden;
    }

    &__box {
      width: 224px;
      margin-right: 59px;
      position: relative;
      flex-grow: 1;
      -ms-flex-negative: 0;
      flex-shrink: 0;
      padding-bottom: 20px;

      @include media-breakpoint-up(xl) {
        flex-shrink: 1;
        padding-bottom: 0;
      }

      &-title {
        font-family: 'Squad Heavy', sans-serif;
        padding-top: 24px;
        padding-bottom: 8px;
        font-size: 20px;
        font-weight: normal;
        line-height: 28px;

        @include media-breakpoint-up(lg) {
          padding-bottom: 16px;
          font-weight: 800;
          font-size: 24px;
          line-height: 32px;
        }
      }

      &-description {
        font-family: "Source Sans Pro", sans-serif;
        font-weight: normal;

        @include media-breakpoint-up(lg) {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
  }

  &-options {
    position: relative;
    display: flex;
    z-index: 1;

    @include media-breakpoint-up(lg) {
      height: 100%;
    }

    &__image {
      position: absolute;
      width: 100%;
      height: 1115px;
      background: url('../assets/images/media-mobile.png') no-repeat right;
      background-size: contain;
      z-index: 99;

      @include media-breakpoint-up(sm) {
        height: 100%;
      }
      @include media-breakpoint-up(lg) {
        position: relative;
        height: 806px;
        background: url('../assets/images/media.png') no-repeat left;
        background-size: contain;
        width: 28%;
        margin-right: 30px;
      }
    }

    &__container {
      width: 97%;
      margin-top: 55px;
      background: rgba(255, 255, 255, 0.8);
      padding-right: 16px;
      padding-left: 16px;
      z-index: 100;

      @include media-breakpoint-up(md) {
        max-width: 96%;
        padding: 0;
        margin: 0 auto;
      }

      @include media-breakpoint-up(lg) {
        width: 72%;
      }
    }

    &__title {
      font-family: 'Squad Heavy', sans-serif;
      width: 100%;
      font-weight: 800;
      font-size: 32px;
      line-height: 48px;
      letter-spacing: -0.4px;
      padding-bottom: 57px;
      padding-top: 32px;

      @include media-breakpoint-up(lg) {
        width: 800px;
        font-weight: 800;
        font-size: 40px;
        line-height: 48px;
        padding-bottom: 43px;
        padding-top: 45px;
      }
    }

    &__box-container {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;

      @include media-breakpoint-up(lg) {
        flex-direction: row;
      }
    }

    &__box {
      width: 256px;
      padding-bottom: 48px;

      @include media-breakpoint-up(lg) {
        margin-right: 40px;
        padding-bottom: 80px;
      }

      &-title {
        font-family: 'Squad Heavy', sans-serif;
        padding-bottom: 16px;
        color: #1B1F27;
        font-weight: 800;
        font-size: 24px;
        line-height: 32px;
      }

      &-description {
        font-family: 'Source Sans Pro', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: #494949;
      }
    }

    &__btn-container {
      padding-top: 35px;
      padding-bottom: 32px;
    }
  }

  &-partners {

    &__title {
      font-family: 'Squad Heavy', sans-serif;
      font-weight: 800;
      font-size: 40px;
      line-height: 48px;
      letter-spacing: -0.4px;
      margin-top: 68px;
      margin-bottom: 40px;
      text-align: center;

      @include media-breakpoint-up(lg) {
        margin-top: 75px;
        margin-bottom: 37px;
        font-weight: 800;
        font-size: 40px;
        line-height: 48px;
      }
    }

    &__img {
      filter: grayscale(100);
      padding-right: 21px;
      padding-bottom: 40px;
      zoom: 0.7;

      &.active {
        filter: none;
      }

      @include media-breakpoint-up(lg) {
        padding-right: 60px;
        padding-bottom: 75px;
        zoom: 1;
      }
    }

    &__reviews {
      position: relative;
      font-family: Source Sans Pro, sans-serif;
      margin-top: 13px;
      color: #1B1F27;

      .triangle-rounded {
        background: url('../assets/images/triangle-rounded.svg');
        width: 30px;
        height: 15px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        z-index: 100;
      }

      &-container {
        max-width: 992px;
        background: #F7F7F7;
        border-radius: 24px;
        padding: 0 36px 15%;
        height: auto;

        @include media-breakpoint-up(md) {
          height: 264px;
          padding: 0 79px 15%;
        }

        @include media-breakpoint-up(lg) {
          height: 264px;
          padding: 0 79px;
        }
      }
      &-info-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100%;
      }
      &-avatar {
        width: 96px;
        height: 96px;
        margin-top: 40px;
        margin-bottom: 16px;

        &._a-market {
          background: url('../assets/images/avatar-a-market.png') no-repeat center;
        }
        &._techport {
          background: url('../assets/images/avatar-techport.png') no-repeat center;
        }
        &._kolesa-darom {
          background: url('../assets/images/avatar-kolesa.png') no-repeat center;
        }

        @include media-breakpoint-up(lg) {
          margin-top: 44px;
        }
      }
      &-name {
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 4px;
      }
      &-position {
        font-size: 14px;
        line-height: 16px;
        margin-bottom: 32px;

        @include media-breakpoint-up(lg) {
          margin-bottom: 64px;
        }
      }
      &-review {
        font-style: italic;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: #4A4A4A;
        text-align: center;

        @include media-breakpoint-up(lg) {
          font-size: 20px;
          line-height: 32px;
          text-align: left;
        }
      }
    }
  }

  &-variant {
    margin-bottom: 68px;

    @include media-breakpoint-up(lg) {
      margin-bottom: 132px;

    }

    .phone-slider {
      position: relative;
      background: url('../assets/images/iphone.png') no-repeat center;
      background-size: contain;
    }

    &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 90%;
      margin: 131px auto;
    }

    &__box-container {
      display: flex;
      flex-direction: column;
    }

    &__image, &__slider-image {
      width: 100%;
      height: 100%;
      min-height: 622px;
      transition: 0.3s linear;
      position: relative;
      margin: 0 auto;
      background-position: center;

      @include media-breakpoint-up(lg) {
        height: 100%;
        min-height: 840px;
        position: relative;
        margin: 0 auto;
      }


      &.variantOne {
        background: url('../assets/images/mobile.png') no-repeat center;
        background-size: contain;
      }

      &.variantTwo {
        background: url('../assets/images/mobile1.png') no-repeat center;
        background-size: contain;
      }

      &.variantThree {
        background: url('../assets/images/mobile2.png') no-repeat center;
        background-size: contain;
      }
    }

    &__box {
      padding: 23px 0 22px;
      display: flex;
      font-family: 'Source Sans Pro', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 130%;
      color: #000000;
      box-sizing: border-box;
      text-align: left;
      border: 1px solid transparent;

      &:last-child {
        margin-bottom: 32px;

        @include media-breakpoint-up(lg) {
          margin-bottom: 0;
          padding-bottom: 0;
        }
      }

      @include media-breakpoint-up(lg) {
        cursor: pointer;
        font-size: 20px;
        line-height: 130.8%;
        color: #000000;
        width: 479px;
        margin-bottom: 19px;
      }

      .variant-icon {
        font-family: 'Squad Heavy', sans-serif;
        font-weight: 800;
        font-size: 40px;
        letter-spacing: -0.4px;
        color: #9BD46A;
        float: left;
        padding: 5px 22px 29px 26px;

        @include media-breakpoint-up(lg) {
          padding: 0 16px 29px 30px;
        }
      }

      .variant-text {
        margin-right: 20px;
        @include media-breakpoint-up(lg) {
          width: 425px;
          margin-right: auto;
        }

        b {
          font-family: "Squad Heavy", sans-serif;
        }
      }

      &.selected {
        border: 0;

        @include media-breakpoint-up(lg) {
          border: 1px solid #52AE30;
          border-radius: 4px;
        }
      }
    }

    &__title {
      font-family: 'Squad Heavy', sans-serif;
      font-weight: 800;
      font-size: 32px;
      line-height: 40px;
      width: 100%;
      margin-top: 55px;
      letter-spacing: 0;
      margin-bottom: 24px;

      @include media-breakpoint-up(lg) {
        font-weight: 800;
        width: 400px;
        margin-top: 0;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: -0.4px;
        margin-bottom: 33px;
      }
    }

    &__btn-container {
      margin-left: 30px;
      align-self: normal;

      &-mobile {
        margin-top: 27px;
        text-align: center;
        padding-left: 0;

        .product-btn {
          padding: inherit;
        }
      }
    }

    .swiper-container {
      display: block;

      @include media-breakpoint-up(lg) {
        display: none;
      }
    }
  }

  &-connection {

    &__title {
      font-family: 'Squad Heavy', sans-serif;
      font-weight: 800;
      font-size: 40px;
      line-height: 48px;
      letter-spacing: -0.4px;
      margin-top: 68px;
      margin-bottom: 40px;
      text-align: center;

      @include media-breakpoint-up(lg) {
        margin-top: 112px;
        margin-bottom: 56px;
        font-weight: 800;
        font-size: 32px;
        line-height: 40px;
      }
    }

    &__box-container {
      display: flex;
      flex-direction: column;
      align-items: center;

      @include media-breakpoint-up(lg) {
        flex-direction: row;
        align-items: baseline;
      }

      .product-connection__box:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          width: 64px;
          top: 25px;
          border-top: 1.4px dashed #52AE30;
          display: none;

          @include media-breakpoint-up(lg) {
            display: block;
          }
        }
      }

      .product-connection__box:nth-child(1) {
        &:after {
          right: 0;
        }
      }

      .product-connection__box:nth-child(2) {
        &:after {
          right: -25px;
        }
      }
    }

    &__icon-block {
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: center;
      max-width: 50%;
      margin: 0 auto 30px;
    }

    &__img {
      width: 50px;
      height: 50px;
    }

    &__number {
      position: relative;
      margin-right: 9px;
      top: -10px;
      font-family: "Source Sans Pro", sans-serif;
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
      color: $white;
      background: #52AE30;
      display: flex;
      width: 24px;
      height: 24px;
      border-radius: 50%;
      box-sizing: border-box;
      align-items: center;
      justify-content: center;
    }

    &__box:nth-child(1) {
      .product-connection__img {
        background: url('../assets/images/task-list-pen.png') no-repeat;
      }
    }

    &__box:nth-child(2) {
      .product-connection__img {
        background: url('../assets/images/laptop-user.png') no-repeat;
      }
    }

    &__box:nth-child(3) {
      .product-connection__img {
        background: url('../assets/images/clap.png') no-repeat;
      }
    }

    &__box {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      @include media-breakpoint-up(lg) {
        width: 100%;
      }

      &-title {
        font-family: 'Squad Heavy', sans-serif;
        padding-top: 24px;
        padding-bottom: 8px;
        font-size: 20px;
        font-weight: normal;
        line-height: 28px;
        display: none;

        @include media-breakpoint-up(lg) {
          padding-bottom: 16px;
          font-weight: 800;
          font-size: 24px;
          line-height: 32px;
          display: block;
        }
      }

      &-description {
        font-family: "Source Sans Pro", sans-serif;
        margin-bottom: 32px;
        width: 288px;

        .connection-text {
          font-weight: normal;
          text-align: center;
          font-size: 16px;
          line-height: 24px;
          color: #000000;
        }
      }
    }

    &__btn-container {
      margin-top: 32px;
      padding-bottom: 73px;
      @include media-breakpoint-up(lg) {
        margin-top: 80px;
        padding-bottom: 127px;
      }
    }
  }

  &-questions {
    padding-top: 48px;
    padding-bottom: 30px;
    min-height: 823px;


    @include media-breakpoint-up(lg) {
      padding-top: 132px;
      min-height: 823px;
      height: 100%;
    }

    &__title {
      font-family: 'Squad Heavy', sans-serif;
      font-size: 40px;
      width: 173px;
      line-height: 48px;
      letter-spacing: -0.4px;
      margin-top: 0;
    }

    &__question {
      position: relative;
      border-bottom: 1px solid #F1F1F1;

      &:first-child {
        @include media-breakpoint-up(lg) {
          margin-top: -15px;
        }
      }

      .question-title-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;

        &.expanded {
          .question-icon {
            background: url('../assets/images/down.svg') no-repeat;
          }
        }

        &.expanded + .product-questions__question-description {
          height: auto;
          max-width: 672px;
          visibility: visible;
          opacity: 1;
          padding-bottom: 24px;
        }
      }

      .question-icon {
        background: url('../assets/images/up.svg') no-repeat;
        width: 25px;
        height: 10px;
        margin-left: 15px;
      }

      &-title {
        font-family: 'Source Sans Pro', sans-serif;
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        padding-top: 24px;
        padding-bottom: 24px;
        width: 100%;

        @include media-breakpoint-up(lg) {
          font-weight: bold;
          font-size: 20px;
          line-height: 28px;
          color: #1B1F27;
        }
      }

      &-description {
        font-family: 'Source Sans Pro', sans-serif;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        padding-bottom: 0;
        height: 0;
        visibility: hidden;
        opacity: 0;
        transition: opacity .35s ease-in-out;
        overflow: hidden;

        @include media-breakpoint-up(lg) {
          padding-top: 0;
        }
      }
    }
  }

  &-info {
    background: #EFF2F9;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 40px 16px 49px 16px;

    @include media-breakpoint-up(lg) {
      height: 209px;
      padding: 0;
    }

    &__text {
      font-family: 'Source Sans Pro', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
    }
  }
}