body {
  color: #000000;
  width: 100%;
  background: $white;
  font-style: normal;
  margin: 0;
}

.content-box {
  padding-right: 32px;
  padding-left: 32px;
  display: flex;
  flex-direction: column;
  align-items: initial;
  box-shadow: 0 16px 48px rgba(73, 92, 136, 0.15);
  border-radius: 24px;
  margin-bottom: 32px;
  margin-right: 8px;
  margin-left: 8px;

  @include media-breakpoint-up(md) {
    max-width: 65%;
    margin: 0 auto 32px;
    padding-bottom: 48px;
  }

  @include media-breakpoint-up(lg) {
    max-width: 94%;
    margin: 75px auto 0;
    height: 457px;
    align-items: center;
  }
}

.container {
  max-width: 100%;

  @include media-breakpoint-up(md) {
    max-width: 90%;
  }

  &.only-desktop-view {
    display: none;

    @include media-breakpoint-up(lg) {
      display: block;
    }
  }
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}