.product-btn {
  font-family: 'Source Sans Pro', sans-serif;
  border-radius: 32px;
  padding: 13px 31px 15px;
  height: 56px;
  border: 0;
  width: 100%;
  cursor: pointer;
  font-size: 16px;
  line-height: 17px;
  outline: none;

  @include media-breakpoint-up(md) {
    width: max-content;
    font-size: 16px;
    line-height: 24px;
  }

  @include media-breakpoint-up(lg) {
    padding: 13px 32px 15px;
  }

  &__orange {
    background: #FF6600;
    color: $white;
    font-weight: 600;
    letter-spacing: 0.2px;
    font-size: 16px;
    line-height: 24px;
    width: auto;
    filter: drop-shadow(0px 15px 20px rgba(0, 0, 0, 0.1));

    &:hover {
      background: #FF802B;
    }

    &:active {
      background: #CC5200;
    }
  }

  &__green {
    background: #52AE30;
    color: $white;
    box-shadow: 0 15px 20px rgba(0, 0, 0, 0.1);
    font-weight: 600;

    &:hover {
      background: #6ECD4A;
    }

    &:active {
      background: #428B26;
    }
  }

  &__outline {
    background: $white;
    color: #52AE30;
    border: 1px solid #52AE30;
    font-weight: 600;
    font-size: 16px;

    &:hover,
    &:active {
      border-width: 2px;
      padding: 12px 31px 12px 31px;
    }

    &:hover {
      border-color: #6ECD4A;
      color: #6ECD4A;
    }

    &:active {
      background: #DBF3D2;
      border-color: #52AE30;
      color: #52AE30;
    }
  }
}